#body_Accueil{
    margin:0;
	padding:0;
	width:100%;
    height: 100vh;
}

.sub-body_Accueil {
	margin:0;
	padding:0;
	width:100%;
	text-align:center;
	background: url(/portfolio/static/media/IMG_3382.f4c05b31.jpg) no-repeat 50% 50%;
	background-size: cover;
	height: 100vh;
	display: table;
}

.text_Theo{
    font-family:'Times New Roman';
    font-size:176.39px;
    line-height:1.25;
    stroke-width:.35278;
}

.tspan_Theo{
    fill:#fff;
    font-size:44.097px;
    stroke-width:.35278;
    stroke:#ffffff;
    stroke-dasharray: 250;
    opacity:10;
    -webkit-animation: animationTheo 2.5s cubic-bezier(0,0.23,1,.1);
            animation: animationTheo 2.5s cubic-bezier(0,0.23,1,.1);
}

@-webkit-keyframes animationTheo {
	0% {
		opacity:0;
		fill: none;
		stroke-dashoffset: 250;
	}
	30% {
		opacity:10;
		fill: none;
		stroke-dashoffset: 250;
	}
	90%{
		fill: rgba(255,255,255,0);
	}
	100% {
		opacity:10;
		fill: rgba(255,255,255,1);
		stroke-dashoffset:0;
	}
}

@keyframes animationTheo {
	0% {
		opacity:0;
		fill: none;
		stroke-dashoffset: 250;
	}
	30% {
		opacity:10;
		fill: none;
		stroke-dashoffset: 250;
	}
	90%{
		fill: rgba(255,255,255,0);
	}
	100% {
		opacity:10;
		fill: rgba(255,255,255,1);
		stroke-dashoffset:0;
	}
}

.wrapper_Accueil {
	position:absolute;
	top: 45%;
	left:50%;
	transform:translate(-42%,-50%);
}

.svg_Accueil {
    height: auto;
	max-width: 100%;
	transition: opacity 1.5s;
  }

#title_Accueil{
    width: 100%;
    top: 40%;
    position: fixed;
    z-index: 10;
  }

#triangle-up {
    background-image: url(/portfolio/static/media/IMG_3382.f4c05b31.jpg);background-size: cover;background-attachment: fixed;
    width: 0;
    height: 0;
    border-left: 100vw solid transparent;
    border-right: 0 solid transparent;
    border-bottom: 100vh solid transparent;
    transition: 2s ease-in-out;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    }

#triangle-down {
    background-image: url(/portfolio/static/media/IMG_3382.f4c05b31.jpg);background-size: cover;background-attachment: fixed;
    width: 0;
    height: 0;
    border-left: 0 solid transparent;
    border-right: 100vw solid transparent;
    border-top: 100vh solid transparent;
    transition: 2s ease-in-out;
    float: left;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    }

#button_Accueil {
    position: fixed;
    z-index: 2;
    opacity: 1;
    width: 11vh;
    height: 5.5vh;
    bottom: 100px;
    left: calc(50% - 5.5vh);
    color: #fff;
    border-radius: 50%;
    transition: .5s ease-in-out;
    cursor: pointer;
    }

#button_Accueil:focus {
    outline: 0;
    }

#button_Accueil:hover {
    transform: scale(1.5);
    }

#button_Accueil:active {
    transform: scale(1.1);
    }

#triangle-down.usenand {
    border-left: 0 solid transparent;
    border-right: 0 solid transparent;
    border-top: 100vh solid rgb(195, 199, 237);
    transition: 2s ease-in-out;
    }

#triangle-up.usenand {
    border-left: 0 solid transparent;
    border-right: 0 solid transparent;
    border-bottom: 100vh solid rgb(230, 171, 198);
    transition: 2s ease-in-out;
    }

.opacity0{
    opacity: 0!important;
    }

.opacity1{
    opacity: 1!important;
}

.chevron-right_Accueil{
    -webkit-animation: pingpingright 5s infinite;
            animation: pingpingright 5s infinite;
}

@-webkit-keyframes pingpingright {
	0% {
        transform: translateX(0%);
	}
	10% {
        transform: translateX(50%) scale(1.5);
    }
    20%{
        transform: translateX(25%) scale(1);
    }
	30%{
        transform: translateX(50%) scale(1.5);
    }
    40%{
        transform: translateX(0%) scale(1);
	}
}

@keyframes pingpingright {
	0% {
        transform: translateX(0%);
	}
	10% {
        transform: translateX(50%) scale(1.5);
    }
    20%{
        transform: translateX(25%) scale(1);
    }
	30%{
        transform: translateX(50%) scale(1.5);
    }
    40%{
        transform: translateX(0%) scale(1);
	}
}

.chevron-left_Accueil{
    -webkit-animation: pingpingleft 5s infinite;
            animation: pingpingleft 5s infinite;
}

@-webkit-keyframes pingpingleft {
	0% {
        transform: translateX(0%);
	}
	10% {
        transform: translateX(-50%) scale(1.5);
    }
    20%{
        transform: translateX(-25%) scale(1);
    }
	30%{
        transform: translateX(-50%) scale(1.5);
    }
    40%{
        transform: translateX(0%) scale(1);
	}
}

@keyframes pingpingleft {
	0% {
        transform: translateX(0%);
	}
	10% {
        transform: translateX(-50%) scale(1.5);
    }
    20%{
        transform: translateX(-25%) scale(1);
    }
	30%{
        transform: translateX(-50%) scale(1.5);
    }
    40%{
        transform: translateX(0%) scale(1);
	}
}
.body_Menu{
    background: rgb(14,12,23);
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    overflow: hidden;
    font-family: 'Times New Roman', Times, serif;
}

.ul_Menu{
    height: 25vh;
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.li_Menu{
    margin-top: 3px;
}

.ul_Menu .li_Menu{
    list-style: none;
}

.ul_Menu .li_Menu::before{
    content: attr(datatype);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color:#fff;
    font-size: 6em;
    font-weight: 700;
    pointer-events: none;
    opacity: 0;
    letter-spacing: 20px;
    transition: 0.5s;
    white-space: nowrap;
}

.ul_Menu .li_Menu:hover::before{
    opacity: 0.50;
    letter-spacing: 20px;
}

.ul_Menu .li_Menu .a_Menu{
    position: relative;
    display: inline-block;
    padding: 6px 15px;
    font-size: x-large;
    text-decoration: none;
    background: #14619E;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    overflow: hidden;
    transition: 0.5s;
    z-index: 1;
} 

.ul_Menu .li_Menu .a_Menu:hover{
    transform: scale(1.4);
    z-index: 1000;
    background: rgb(230, 171, 198);
    opacity: 1;
}

.ul_Menu .li_Menu .a_Menu::before{
    content: '';
    position: absolute;
    top: 0;
    left: -100px;
    width: 80px;
    height: 100%;
    background: linear-gradient(90deg,transparent, rgb(255,255,255), transparent);
    transform: skewX(35deg);
    transition: 0s;
}

.ul_Menu .li_Menu .a_Menu:hover::before{
    transition: 0.5s;
    transition-delay: 0.2s;
    -webkit-animation: zoumzoum 2s infinite;
            animation: zoumzoum 2s infinite;
}

@-webkit-keyframes zoumzoum {
	10% {
        left: calc(0% - 100px);
	}
	30% {
        left: calc(100% + 100px);
    }
    100%{
        left: calc(100% + 100px);
    }
}

@keyframes zoumzoum {
	10% {
        left: calc(0% - 100px);
	}
	30% {
        left: calc(100% + 100px);
    }
    100%{
        left: calc(100% + 100px);
    }
}
